import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Person from '@/components/person/Person'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main,
      meta: {title: 'Stock Trader'}
    },
    {
      path: '/person',
      name: 'Person',
      component: Person,
      meta: {title: 'Stock Trader'}
    }
  ]
})



